.calc {
    width: 280px;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform : translate(-50%, -50%);
            transform : translate(-50%, -50%);
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.1);
    font-family: 'Noto Sans KR', sans-serif;
    padding-top: 10px;
}

.calc button {
    padding: 0;
    border: none;
    outline: none;
    display: block;
    cursor: pointer;
}

.calc .record {
    padding: 0 20px;
    width: 240px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.calc .record select {
    all: unset;
    width: 80px;
    overflow: hidden;
    line-height: 20px;
    font-size: 12px;
    border-bottom: 1px solid #262626;
}
