    width: 240px;
    height: 20px;
    line-height: 20px;
    text-align: right;
    margin-top: 20px;
}

.resWindow {
    width: 240px;
    height: 50px;
    background-color: #fff;
    color: #262626;
    line-height: 50px;
    text-align: right;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 20px;
    font-weight: 500;
    padding: 0 20px;
    font-size: 1.875em;
    position: relative;
}
.resWindow::after {
    display: block;
    position: absolute;
    width: 240px;
    height: 1px;
    content: "";
    left: calc(50%-120px);
    background-color: #ccc;
    bottom: 0px;
}

.calcBtn {
    width: 240px;
    padding: 0 20px;
    display: flex;
    justify-content: flex-start;
}

.numberBox {
    width: 180px;
