    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    left: calc(50% - 20px);
    top: calc(50% - 20px);
    line-height: 40px;
    color: #999;
    text-align: center;
}

.number .num:hover span{
    color: #262626;
}
.number .num:active span{
    color: #262626;
    background-color: #eee;
